<template>
    <div class="x-container">
        <div class="position">
            <p class="position-title">设备位置</p>
            <van-row class="position-group">
                <van-col span="8">
                    <van-button @click="onSeat(1)" :class="s1Style" square type="primary">主驾</van-button>
                </van-col>
                <van-col span="8">
                    <van-button @click="onSeat(2)" :class="s2Style" square type="primary">副驾</van-button>
                </van-col>
                <van-col span="8">
                    <van-button @click="onSeat(3)" :class="s3Style" square type="primary">贵宾</van-button>
                </van-col>
            </van-row>
        </div>

        <div class="problem">
            <p class="problem-title">设备问题</p>
            <van-checkbox-group class="problem-group" v-model="result">
                <van-checkbox class="problem-group-item"
                              v-for="(item) in problems"
                              :key="item"
                              :name="item"
                              shape="square"
                >
                    {{ item }}
                </van-checkbox>
            </van-checkbox-group>
        </div>

        <div class="info">
            <van-field class="info-detail" type="textarea" placeholder="其他问题描述" v-model="other"></van-field>
        </div>

        <van-button class="ok">提交</van-button>

    </div>
</template>

<script>
    import {Checkbox, CheckboxGroup, Button, Row, Col, Field} from 'vant';
    import {REPAIR, DRIVER_VERIFY, FETCH_OPENID} from '../../common/config'
    import {get, post} from '../../common/http'
    import store from '../../storage'
    import { Toast } from 'vant';

    export default {
        components: {
            [CheckboxGroup.name]: CheckboxGroup,
            [Checkbox.name]: Checkbox,
            [Row.name]: Row,
            [Col.name]: Col,
            [Button.name]: Button,
            [Field.name]: Field
        },

        data() {
            return {
                result: [],
                other: '',
                problems: [
                    '支付成功，设备不工作',
                    '小程序无法连接设备',
                    '设备工作时长不准确',
                    '设备长时间工作，无法停止'
                ],
                selected: 0,
                data: {}
            };
        },

        computed: {
            s1Style() {
                return this.selected === 1 ? 'position-group-seat active' : 'position-group-seat'
            },
            s2Style() {
                return this.selected === 2 ? 'position-group-seat active' : 'position-group-seat'
            },
            s3Style() {
                return this.selected === 3 ? 'position-group-seat active' : 'position-group-seat'
            },

            totalPrice() {
                return this.goods.reduce((total, item) => total + (this.checkedGoods.indexOf(item.id) !== -1 ? item.price : 0), 0);
            }
        },

        methods: {
            onSeat(type) {
                if (type === this.selected) {
                    this.selected = 0
                } else {
                    this.selected = type
                }
            },
            onSubmit() {
                const radio_problem = this.result.join(",")
                const text_problem = this.other
                const openid = this.data.openid
                const param = {position: this.selected, radio_problem, text_problem, openid}
                post(REPAIR, param, () => {
                    Toast.success("提交成功")
                })
            }
        },
        created() {
            const data = store.fetch()
            if (data && data.hasOwnProperty("openid")) {
                // has open id?
                this.data = data
                if (!data.hasOwnProperty("id")) {
                    const params = {openId: data.openid}
                    // is bind?
                    post(DRIVER_VERIFY, params, res => {
                            let resData = res;
                            resData.openid = data.openid
                            store.save(resData)
                            this.data = resData
                        }, () => this.$router.push({name: 'bind', query: { from:  'device' }})
                    );
                }
            } else {
                const code = this.$route.query.code
                if (code) {
                    const params = {code}
                    get(FETCH_OPENID, params, res => {
                        this.data.openid = res.openid;
                        store.save(this.data)
                        post(DRIVER_VERIFY, params, res => {
                                let resData = res;
                                resData.openid = data.openid
                                store.save(resData)
                            }, () => this.$router.push({name: 'bind', query: { from:  'device' }})
                        );
                    })
                    ;
                } else {
                    Toast.fail("无法获得授权信息，请退出重试")
                }
            }
        }
    };
</script>

