import { Toast } from 'vant';
import Vue from 'vue';

export const post = (url, params, success, fail) => {
    Vue.http.post(url, {params}, {emulateJSON: true}).then(res => {
        if (res.status === 200) {
            if (res.data.hasOwnProperty("error_code")) {
                if (res.data.error_code !== 0) {
                    Toast.fail(res.data.msg)
                    fail && fail(res.data.msg)
                } else {
                    success && success(res.data.data)
                }
            } else {
                success && success(res.data)
            }
        }

    }, () => {
        Toast.fail('网络请求出错')
        fail && fail('网络请求出错')
    });
}

export const get = (url, params, success, fail) => {
    Vue.http.get(url, {params}).then(res => {
        window.console.log('http res', res)
        if (res.status === 200) {
            if (res.data.hasOwnProperty("error_code")) {
                if (res.data.error_code !== 0) {
                    Toast.fail(res.data.msg)
                    fail && fail(res.data.msg)
                } else {
                    success && success(res.data.data)
                }
            }else {
                window.console.log('has not error code')
                success && success(res.data)
            }
        }

    }, () => {
        Toast.fail('网络请求出错')
        fail && fail('网络请求出错')
    });
}
